@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');

h1,h2,h3,p {
    font-family: "Manrope", serif;
    font-optical-sizing: auto;
    font-style: normal;
    margin-top: 0rem;
}

h1,h2,h3{
    color: var(--h);
}

h3{
    font-size: 14px;
}

p{
    color: var(--p);
    font-weight: 300;
    font-size: 10px;
}

a, a:visited, a:active{
    text-decoration: none;
}